.card {
    border-radius: 10px;
    height: 100%;
    aspect-ratio: 1/1;
    justify-items: center;
    position: relative;
}
.card:hover {
    background: linear-gradient(180deg, #ff6060 85%, #000000a6 100%);
    cursor: pointer;
}

.card-image {
    border-radius: 10px;
    height: 100%;
    object-fit: cover;
    width: 100%;
}
.card:hover > .card-image {
    opacity: 0.30;
}

.card-sombre {
    background: linear-gradient(180deg, #ffffff00 0%, #00000080 100%);
    border-radius: 10px;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

.card-titre {
    bottom: 20px;
    color: #ffffff;
    font-size: 18px;
    left: 20px;
    margin-right: 20px;
    position: absolute;
}

@media screen and (max-width: 1000px) {
    .card {
        min-height: 255px;
    }
    .card-titre {
        width:50%;
    }
}