.Fiche {
    color: #ff6060;
}

.logements-propietaire {
    display: inline-flex;
    width: 100%;
}

.logements-propietaire > div {
    flex: 1 0 auto;
}

.information-logements {
    flex-direction: column;
}

.titre-logement {
    font-size: 36px;
}

.endroit-logement {
    display: block;
    font-size: 18px;
    margin-bottom: 25px;
}

.information-propietaire {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 25px;
    margin-right: 20px;
}

.nom-proprietaire {
    font-size: 18px;
    margin-right: 10px;
    text-align: right;
    width: 100px;
}

.photo-propietaire {
    border-radius: 50%;
    height: 64px;
    object-fit: cover;
    width: 64px;
}

.note {
    align-items: center;
    display: flex;
    justify-content: flex-end;
}

.etoile {
    height: 30px;
    width: 30px;
}

.description-equipements {
    display: inline-flex;
    gap: 0 75px;
    line-height: 22.5px;
    list-style: none;
    margin-top: 25px;
    width: 100%;
}

.description-equipements > .dropdown {
    width: 50%;
}
.hostInfo-container {
	display: flex;
	width: 100%;
	justify-content: space-between;

	@media screen and (min-width: 1000px) {
		flex-direction: row;
		align-items: flex-end;
	}
}
@media screen and (max-width: 1000px) {
    .logements-propietaire {
        display: block;
    }
    .hostInfo-container {
        display:flex;
        flex-direction: column;
    }
    .titre-logement {
        display: block;
        font-size: 24px;
        margin-bottom: 5px;
    }

    .endroit-logement {
        font-size: 14px;
        margin-bottom: 10px;
    }

    .tags {
        display: inline-flex;
        flex-wrap: wrap;
        gap: 10px 0;
    }
.tags-container{
    display:flex;
    width:100%;

}

    .tags > .tag {
        margin-left: 0px;
        min-width: 50px;
    }   
    .note{
        position:relative;
        left:0;

    }

    .proprietaire-note {
        display: inline-flex;
        flex-direction: row-reverse;
        justify-content: flex-end;
        margin-top: 25px;
        width: 100%;
    }

    .information-propietaire {
        margin-bottom: unset;
        width: 85%;
    }

    .photo-propietaire {
        height: 32px;
        width: 32px;
    }

    .etoile {
        height: 25px;
        width: 25px;
    }

    .description-equipements {
        display: block;
    }

    .description-equipements > .dropdown {
        width: 100%;
    }
    
    .dropdown {
        margin-bottom: 20px;
    }
    
}
